<template>
  <div>
    <Loadings v-if="data ==null"></Loadings>
    <div class="container" v-else>

      <Empty v-if=" data.length == 0"></Empty>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        class="list"
        v-else
      >
        <router-link
          :to="{ path: '/custDetails/'+item.cust_no }"
          class="item"
          v-for="(item, index) in data"
          :key="index"
        >
          <div class="info">
            <div class="company flex-sb">
              {{item.company}}
              <span class="time">过期{{item.overTime}}天</span>
            </div>
            <div class="name">地址:{{item.address}}</div>

            <div class="name">
              姓名:{{item.name}}
              <span style="padding-left:15px;">电话:{{item.phone}}</span>
            </div>
            <div class="name">客户类型:{{item.status}}</div>
            <div class="name">要求联系日期:{{formatDate(item.visiting_time)}}</div>
            <div class="name">业务员:{{item.name}}</div>
          </div>
        </router-link>
      </van-list>
    </div>
  </div>
</template> 

<script>
import { visitingOverdue } from "@api/public";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
import { Icon, List } from "vant";
export default {
  name: "ContactOverdue",
  components: {
    Empty,
    Loadings,
    [Icon.name]: Icon,
    [List.name]: List,
  },
  props: {},
  data: function () {
    return {
      data: [],
      isActive: 1,
      pay_no: 0,
      pay_yes: 0,
      page: "",
      loading: false,
      finished: false,
    };
  },
  created() {

  },
  mounted: function () {},
  methods: {
    selectType(e) {
      if (e != this.isActive) {
        this.page = "";
        this.isActive = e;
        this.visitingOverdue();
      }
    },
    visitingOverdue() {
      visitingOverdue({ is_pay: this.isActive, page: this.page }).then(
        (res) => {
          var overTime;
          res.data.data.forEach((item) => {
            overTime = Math.floor(
              (Date.parse(new Date()) / 1000 - item.visiting_time) / 86400
            );
            item.overTime = overTime;
          });
          if (this.page != "") {
            this.data = this.data.concat(res.data.data);
          } else {
            this.data = res.data.data;
            this.pay_no = res.data.pay_no; 
            this.pay_yes = res.data.pay_yes;
          }
          this.finished = res.data.data.length < 200 ? true : false;
          this.loading = res.data.data.length < 200 ? true : false;
          if (res.data.data.length == 200) {
            this.page = this.data[this.data.length - 1].visiting_time;
          }
        }
      );
    },
    formatDate: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
    onLoad() {
      this.visitingOverdue();
    },
  },
};
</script>
<style scoped>
.container {
  box-shadow: 0 0 5px #eee;
  background-color: #fff;

  border-radius: 5px;
}

.container .caption {
  font-weight: 600;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding-left: 10px;

  font-size: 16px;
  color: #333;
}
.select {
  padding-top: 10px;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  background: #fff;
}
.select div {
  padding-bottom: 10px;
  border-bottom: 2px solid #fff;
  margin-bottom: 15px;
}
.select .current {
  border-bottom: 2px solid #092e48;
}
.caption .month {
  height: 25px;
  width: 60px;
  background: #092e48;
  border-radius: 20px;
  color: #fff;
  line-height: 25px;
  font-size: 12px;
}

.caption .month span {
  display: inline-block;
  width: 23px;
  height: 23px;
  background: #fff;
  border-radius: 50%;
  margin-left: 1px;
  margin-right: 4px;
}

.container .list {
  line-height: 43px;
  font-size: 14px;
  padding-top: 50px;
}

.container .list .item {
  padding: 15px 0;
  padding-left: 8px;
  display: block;
  border-bottom: 1px solid #eee;
}

.container .list .item.current {
  background-color: #eee;
}

.container .list img {
  width: 15px;
  margin: 0 5px;
}

.container .list .info {
  line-height: 18px;
  font-size: 12px;
}

.search input {
  background: #fff;
  padding-left: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  height: 30px;
  width: 90%;
}

.search {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  padding: 10px;
  background: #fff;
}

.search img {
  width: 30px;
}

.rank {
  font-weight: bold;
  font-size: 18px;
  padding-left: 10px;
  color: #666;
  padding-right: 15px;
}

.info .company {
  font-size: 16px;
  color: #666;
}

.info .name {
  font-size: 12px;
  margin-top: 5px;
  color: #999;
}

.date {
  padding-left: 10px;
}

.time {
  font-size: 12px;
  padding-right: 10px;
  color: #999;
}
</style>
